<template>
  <div id="tabletRegister">
    <Button type="primary" size="small" @click="getWord" style="position: absolute;right: 58px;top:8px">导出</Button>
    <h2>生态保护红线标识标牌登记表</h2>
    <table border='0' cellpadding='0' cellspacing='0' id="tab">
      <tr>
        <td colspan='6' style="width: 25%">生态红线保护编码</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{tabCon.redlineNum}}</td>
        <td colspan='6' style="width: 25%">标识牌编号</td>
        <td colspan='6' style="width: 25%;font-weight: normal">{{tabCon.number}}</td>
      </tr>
      <tr>
        <td colspan='6'>所在地</td>
        <td colspan='18' style="font-weight: normal">{{tabCon.placeName}}</td>
      </tr>
      <tr>
        <td colspan='6'>标识牌内容</td>
        <td colspan='18'>
          <div style="font-weight: normal;text-align: left;min-height: 106px">{{tabCon.content}}</div></td>
      </tr>
      <tr>
        <td colspan='6'>经度</td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(tabCon.proofLon)}}</td>
        <td colspan='6'>纬度</td>
        <td colspan='6' style="font-weight: normal">{{cacuLonLat(tabCon.proofLat)}}</td>
      </tr>
      <tr>
        <td colspan='4' rowspan='2'>位置略图</td>
        <td colspan='20'>现场照片</td>
      </tr>
      <tr>
        <td colspan='4'>东</td>
        <td colspan='4'>西</td>
        <td colspan='4'>南</td>
        <td colspan='4'>北</td>
        <td colspan='4'>中</td>
      </tr>
      <tr id="images">
        <td colspan='4' v-for="(item, index) in lmPhotos" :key="index">
          <div style="min-height: 120px;display: flex; justify-content: center;align-items: center;">
            <img :src="item.url" alt="" v-if="item.url !== ''"  @click="imgEnlarges()" style="width: 100%">
          </div>
        </td>
      </tr>
      <tr>
        <td colspan='6'>备注</td>
        <td colspan='18'>{{tabCon.remarks}}</td>
      </tr>
      <tr>
        <td colspan='3'>填表人</td>
        <td colspan='3' style="font-weight: normal">{{tabCon.createUser}}</td>
        <td colspan='3'>采集日期</td>
        <td colspan='3' style="font-weight: normal">{{renderTime(tabCon.createDate)}}</td>
        <td colspan='3'>审核人</td>
        <td colspan='3' style="font-weight: normal">{{tabCon.verifyUser}}</td>
        <td colspan='3'>审核日期</td>
        <td colspan='3' style="font-weight: normal">{{renderTime(tabCon.verifyDate)}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import { getLmboardById } from '@/api/acquisitionTable'
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'
import { cacuLonLat, renderTime } from '@/utils/common'
export default {
  name: 'SignRegister',
  data () {
    return {
      /* 获取的数据 */
      tabCon: {
        lmBoardPhotos: []
      }
    }
  },
  computed: {
    /* 处理照片数据 */
    lmPhotos: function () {
      let photosData = [{}, {}, {}, {}, {}, {}]
      for (let i in photosData) {
        for (let j in this.tabCon.lmBoardPhotos) {
          if (this.tabCon.lmBoardPhotos[j].type === 6) {
            photosData[0] = this.tabCon.lmBoardPhotos[j]
          } else if (this.tabCon.lmBoardPhotos[j].type !== 6 && this.tabCon.lmBoardPhotos[j].type === Number(i) + 1) {
            photosData[i * 1 + 1] = this.tabCon.lmBoardPhotos[j]
          }
        }
        if (JSON.stringify(photosData[i]) === '{}') {
          photosData[i].url = ''
        }
      }
      return photosData
    }
  },
  props: {
    tabletId: {}
  },
  watch: {
    tabletId () {
      this.getLmboardById()
    }
  },
  mounted () {
    this.viewers = new Viewer(document.getElementById('images'))
  },
  methods: {
    /* 获取原始界桩列表 */
    getLmboardById () {
      getLmboardById({
        data: {
          id: this.tabletId
        }
      }).then((res) => {
        this.tabCon = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 方位物数据处理 */
    lmMPositionsDate (lmMarkerRelationPositions) {
      if (lmMarkerRelationPositions.length < 3) {
        return lmMarkerRelationPositions.push({})
      } else {
        return lmMarkerRelationPositions.slice(0, 3)
      }
    },
    /* 图片放大显示 */
    imgEnlarges () {
      this.viewers.update()
    },
    /* 日期格式转换 */
    renderTime (date) {
      return renderTime(date)
    },
    /* 经纬度转换为度分秒 */
    cacuLonLat (e) {
      return cacuLonLat(e)
    },
    /* 导出Word文件 */
    getWord () {
      let link = document.createElement('a')
      link.style.display = 'none'
      let url = this.tabCon.fileUrl
      link.href = url
      link.setAttribute('download', url.split('/')[url.split('/').length - 1])
      document.body.appendChild(link)
      link.click()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h2{
    padding:5px 0 0;
    font-weight: normal
  }
  #tab{
    border-collapse: collapse;
    table-layout: fixed;
    text-align: center;
    width: calc(100% - 16px);
    margin: 8px;
    border:solid #d1d1d1; border-width:1px 0 0 1px;
    td{
      padding: 5px;
      font-size: 12px;
      font-weight: bolder;
      border:solid #d1d1d1;
      border-width:0 1px 1px 0;
    }
  }
</style>
